.contact {
    position: relative;
    #map {
        position: absolute;
        width: 100%;
        height: 616px;
        top: 170px;
        &::before {
            content: '';
            display: block;
            position: absolute;
            width: 100%;
            height: 100%;
            background-color: #000000be;
            z-index: 1;
        }
    }
}